* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: $font-primary;
    font-size: $font-size;
    background-color: $bg-color;
    color: $text;
    scroll-behavior: smooth;
}

body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    overflow-x: hidden;
}

section {
    margin-bottom: $section-margin;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: 600;
    margin-bottom: 0,5em;
    color: $title-color;
}

h1 {
    font-size: 5rem;
    line-height: 1.1;
}

h2 {
    font-size: 3.875rem;
    line-height: 1.1;
}

h3 {
    font-size: 2rem;
    line-height: 2;
}

h4 {
    font-size: 1.625rem;
    line-height: 1.23;
}

h5 {
    font-size: 1.5rem;
    line-height: 1.46;
}

h6 {
    font-size: 1.25rem;
    line-height: 1.63;
}

.template-title {
    font-size: min(6vw, 80px);
    font-weight: 800;
    position: relative;
    letter-spacing: -0.03em;
}

.template-description {
    @extend h6;
    color: $text;
    font-weight: 400;
    margin-bottom: 54px;
    max-width: 480px;
    letter-spacing: -0.02em;
}

img {
    height: auto;
    max-width: 100%;
}

a {
    &:hover {
        color: $secondary-color;
    }
}

button {
    cursor: pointer;
}

.btn {
    color: $title-color;
    font-size: 24px;
    border-radius: 50px;
    border: none;
    text-decoration: none;
    padding: 23px 54px;
    background: transparent;
    text-align: center;
    transition: .3s;
    position: relative;
    display: inline-flex;
    align-items: center;

    &:after {
        @include template-light;
        background-image: url(./assets/img/btn-primary-hover.svg);
        width: 0;
        height: 0;
        transition: .5s;
    }

    &:hover {
        color: #fff;

        &::after {
            width: 500%;
            height: 500%;
        }
    }

    svg {
        --size: 28px;
        height: var(--size);
        width: var(--size);
    }

    &.btn-primary {
        background-image: linear-gradient(210deg, #237249 0%, #35c66b 100%);
        border-radius: 82px;
        padding: 25px 54px;
        color: #FFFFFF;
        font-weight: 600;
    }

    &.btn-secondary {
        position: relative;
        background-color: $bg-color;

        &::before {
            content: "";
            position: absolute;
            inset: -2px;
            background-image: linear-gradient(140deg, #BBB3B3 0%, #121517 100%);
            z-index: -1;
            border-radius: 40px;
        } 

        &:after {
            background-image: url(./assets/img/btn-secondary-hover.svg);
        }
    }
}

.container {
    max-width: 1274px;
    padding: 0 12px;
    margin-left: auto;
    margin-right: auto;
}

.btn {
    &.btn-outline {
        width: 84px;
        height: 84px;
        border: 1px solid $primary-color;
        border-radius: 29px;
        display: flex;
        align-items: center;
        justify-content: center;
    
        svg {
            height: 37px;
        }
    }
}

.gradient-text { 
    background-clip: text;
    color: transparent;
    background-image: linear-gradient(204deg, $primary-color 0, $secondary-color 100%);
}

.gradient-backround {
     background-image: linear-gradient(245deg, $primary-color 0, $secondary-color 100%);
}

.svg-srite {
    height: 0;
}

.accent-label {
    --max-size: 167px;
    --size: 14.154vw;
    width: var(--size);
    height: var(--size);
    max-width: var(--max-size);
    max-height: var(--max-size);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: #fff;
    font-size: 55px;
    font-size: min(4.231vw, 55px);
    font-weight: 500;
    position: absolute;
    text-transform: uppercase;
    z-index: 1;
    box-shadow: -4.56px 15.52px 26.48px #143e278a;

    &:before {
        content: "";
        position: absolute;
        width: calc(100% + 20px);
        height: calc(100% + 20px);
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        z-index: -1;
        background: url(./assets/img/bg-discount.svg) center / 100% no-repeat;
    } 
}

.accent-label-sm {
    @extend .accent-label;
    font-size: 32px;
    --max-size: 113px;
}

.white-wrapper {
    background: linear-gradient(230deg, #fff 3.94%, #e5e5e5 64.08%, #fbfbfb 88.62%);

    h1, h2, h3, h4, h5, h6 {
        color: #121517;
    }

    p {
        color: #292C2E;
    }
}

.overflow-hidden {
    overflow: hidden;
}

.g-0 {
    gap: 0;
}

.g-1 {
    gap: 1rem;
}

.g-2 {
    gap: 2rem;
}

.g-3 {
    gap: 3rem;
}

.g-4 {
    gap: 4rem;
}

.g-5 {
    gap: 5rem;
}

::selection {
    color: $bg-color;
    background-color: $primary-color;
}

